<template>
    <div id="view-employee" class="container pt-5">
        <div class="card">
            <div class="card-header">
                <h4>{{gameid}}</h4>
            </div>
            <ul class="list-group list-group-flush">
                <li class="list-group-item">Game ID : {{gameid}}</li>
                <li class="list-group-item">Owner : {{owner}}</li>
                <router-link class="list-group-item btn btn-outline-primary" to="/rtplay">Playboard</router-link>
                <router-link class="list-group-item btn btn-outline-warning" to="/rtthreed">3D (under construction)</router-link>
            </ul>
        </div>
        <hr>
        <div class="card">
            <ul class="list-group list-group-flush">
                <router-link to="/" class="list-group-item btn btn-outline-dark">Back</router-link>
                <router-link v-bind:to="{name: 'edit-employee', params: {gameid: gameid}}" class="list-group-item btn btn-outline-primary">Edit Information</router-link>
                <button @click="doNothing" class="list-group-item btn btn-outline-danger">Delete (to be implemented)</button>
            </ul>

            <div class="fixed-action-btn">
                <router-link v-bind:to="{name: 'edit-employee', params: {gameid: gamid}}" class="btn-floating btn-larg red">
                    <i class="fa fa-pencil"></i>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import {db} from './firebaseInit'
export default {
    name: 'view-employee',
    data () {
        return {
            gameid: null,
            owner: null,
            type: null,
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('games')
        .where('gameid', '==', to.params.gameid)
        .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.gameid = doc.data().gameid 
                    vm.owner = doc.data().owner 
                    vm.type = doc.data().type
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('games')
            .where('gameid', '==', this.$route.params.gameid)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    vm.gameid = doc.data().gameid 
                    vm.owner = doc.data().owner 
                    vm.type = doc.data().type
                })
            })
        },
        deleteEmployee () {
            if(confirm('Sure?')) {
                db.collection('games')
                .where('gameid', '==', this.$route.params.employee_id)
                .get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
            }
        },
        doNothing() {
            console.log("to be implemented")
        },
        anchorManualUpdate: function(){
            this.anchorManual = this.tempAnchorList()
        },
        tempAnchorList: function() {
            var temp = []
            anchorsRef.once('value', function(snapshot) {
                snapshot.forEach(function(childSnapshot) {
                    var child = {
                        Key : childSnapshot.key,
                        Data : childSnapshot.val()
                    }  
                temp.push(child)
                })
            })
            return temp
        },
        
    }
}
</script>


